.menu-list {
    width: 224px;

    .menu-item {
        margin-left: 0;
        font-weight: 500;
        color: #111;
    }
}

.menu-wrapper {
    padding-bottom: 8px;
}

.menu-body {
    overflow-y: auto;
}

//menu item
.menu-item {
    width: 100%;
    padding: 11px 16px;
    line-height: 1.8rem;
    justify-content: flex-start;
    border-radius: 0;

    &:hover {
        background-color: rgba(22, 24, 35, 0.03);
    }

    &.separate {
        border-top: 1px solid rgba(22, 24, 35, 0.12);
    }
}

//header menu languages
.header {
    position: relative;
    height: 50px;
    margin-top: -8px;
    flex-shrink: 0;
}

.back-btn {
    width: 50px;
    height: 100%;
    background-color: transparent;
    cursor: pointer;
}

.header-title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-weight: 500;
}
